.header {
  position: absolute;
  padding: 15px 0;
  top: 0;
  width: 100%;
  z-index: 100;
}

.header__container {
  display: flex;
  justify-content: space-between;
}

.footer {
  padding-top: 30px;
  padding-bottom: 30px;
  background: #f4f7f8;
  text-align: center;
  font-weight: 400;
}

.footer__text {
  font-size: 16px;
  color: #333;
  line-height: 1.4;
  font-weight: 400;
  max-width: 450px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0;
}

.footer__social-snippets {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.footer__social-snippets__item {
  margin: 0 10px 10px;
}

@media (min-width: 992px) {
  .footer__social-snippets__item {
    margin: 0 10px;
  }
}

.footer__legal a {
  margin: 0 10px;
}

.footer__divider {
  padding-top: 30px;
  padding-bottom: 30px;
  position: relative;
}

.footer__divider:before {
  content: '';
  border-top: 1px dotted #999;
  position: absolute;
  top: 50%;
  width: 100%;
  left: 0;
}

.footer__payments {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 10px;
}

@media (min-width: 992px) {
  .footer__payments {
    justify-content: space-between;
  }
}

.footer__payment {
  padding: 0 15px;
}

@media (min-width: 992px) {
  .footer__payment {
    padding: 0;
  }
}

.footer__safety img {
  margin: 0 15px;
}

.libertex-spacer {
  height: 30px;
}

.libertex-spacer--half {
  height: 15px;
}

.libertex-spacer--double {
  height: 60px;
}

.nopadding > [class*="col-"] {
  padding-left: 0 !important;
  padding-right: 0 !important;
}

@media (max-width: 767px) {
  .nopadding-xs > [class*="col-"] {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}

.nopadding-selective > .nopad-col {
  padding-left: 0 !important;
  padding-right: 0 !important;
}

.libertex-cta {
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  background: #ff8000;
  border-radius: 5px;
  box-shadow: 0 3px 0 0 #cc4d00, 0 0 10px 3px rgba(0, 0, 0, 0.3);
  color: #fff;
  display: inline-block;
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  font-weight: 400;
  height: 52.5px;
  line-height: 52.5px;
  max-width: 350px;
  padding: 0 20px;
  text-align: center;
  width: 100%;
  position: relative;
  border: none;
  pointer-events: all;
}

.libertex-cta:hover {
  background: #ffb333;
}

.libertex-cta:active {
  background: #cc4d00;
  box-shadow: 0 0 0 0 #cc4d00;
  transform: translateY(3px);
}

.libertex-cta:disabled {
  filter: grayscale(100%);
  opacity: .4;
  pointer-events: none;
}

.libertex-form {
  display: flex;
  flex-wrap: wrap;
  max-width: 560px;
  margin-left: auto;
  margin-right: auto;
}

.libertex-form__group {
  padding: 2px;
  flex-basis: 50%;
  flex-grow: 2;
  text-align: center;
  position: relative;
}

.libertex-form__input {
  border-radius: 5px;
  background: #fff;
  border: none;
  color: #333;
  padding-left: 10px;
  padding-right: 10px;
  width: 100%;
  height: 37.5px;
}

.libertex-form__input--error {
  border: 1px solid #a00;
}

.libertex-form__input--error + .libertex-form__error {
  display: block;
}

.libertex-form__error {
  display: none;
  background: #fff;
  position: absolute;
  top: 7px;
  left: 5px;
  padding: 3px;
  color: #a00;
}

.libertex-form__success {
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -150%, 0);
  padding: 30px;
  background: #fff;
  border-radius: 5px;
  width: 90%;
  max-width: 450px;
  box-shadow: 0 0 40px 10px rgba(0, 0, 0, 0.3);
  text-align: center;
  opacity: 0;
  pointer-events: none;
}

.libertex-form__success__message {
  color: #ff8000;
  font-size: 24px;
}

.libertex-form--success + .libertex-form__success {
  transform: translate3d(-50%, 50%, 0);
  opacity: 1;
  pointer-events: all;
}

.libertex-hero {
  min-height: 100vh;
  background: url("../images/james-hero.jpg") no-repeat 40% center/cover;
  color: #fff;
  padding-bottom: 30px;
  padding-top: 135px;
  position: relative;
}

@media (min-width: 992px) {
  .libertex-hero {
    padding-top: 105px;
    background: url("../images/james-hero.jpg") no-repeat center center fixed/100%;
  }
}

.libertex-hero__backdrop {
  position: absolute;
  top: 0;
  bottom: 0;
